import { useCallback, useEffect, useRef } from "react";
import { handleIf, PropsEvent } from "../script/event";

/**
 * ref
 * 
 * `useCallback``dependencies`  
 * ref
 * 
 * `useMemo`
 * `useRefCallback`  
 * `dependencies`
 * 
 * @param update 
 * @returns  update
 */
export function useRefCallback<T extends (...args: any[]) => void>(update: T): ((...args: Parameters<T>) => void) {
  const ref = useRef<T>()
  ref.current = update
  return useCallback((...args: Parameters<T>) => {
    ref.current?.(...args)
  }, [])
}

export function useRefValue<T>(updateFunc: (update: (newValue: T) => void) => void): T | undefined {
  const ref = useRef<T>()
  updateFunc((newValue: T) => {
    ref.current = newValue
  })
  return ref.current
}

/**
 * `useEffect``PropsEvent`
 * 
 * `useEffect`
 * ```
 * useEffect(() => {
 *   handleIf(event, value => {
 *     // your handler
 *   })
 * }, [event])
 * ```
 * event
 * `useEffect`
 * 
 * handlerrefhandler  
 * `useEffect``dependencies`handler
 * 
 * @param event  `useEffect`handler
 * @param handler event
 */
export function useEventEffect<T>(event: PropsEvent<T>, handler: (value: T) => void) {
  const handlerRef = useRef<(value: T) => void>()
  handlerRef.current = handler
  useEffect(() => {
    handleIf(event, value => {
      // ref.current should have valid value
      handlerRef.current?.(value)
    })
  }, [event])
}